// –†–µ–Ω–¥–µ—Ä –¥–µ—Å–∫—Ç–æ–ø–Ω–æ–≥–æ —Å–ø–∏—Å–∫—É –∫–∞—Ç–µ–≥–æ—Ä—ñ–π
export function renderCategories(categories) {
  // –Ω–µ –º—É—Ç—É—î–º–æ –≤—Ö—ñ–¥–Ω–∏–π –º–∞—Å–∏–≤ (—â–æ–± —É–Ω–∏–∫–Ω—É—Ç–∏ –ø–æ–±—ñ—á–Ω–∏—Ö –µ—Ñ–µ–∫—Ç—ñ–≤)
  const allCategories = ['All categories', ...categories];
  const markuoCategories = allCategories
    .map(
      category =>
        `<li class="categories-item" data-category="${category}">${category}</li>`
    )
    .join('');
  refs.categoriesList.innerHTML = markuoCategories;
}

// –†–µ–Ω–¥–µ—Ä —Å–ø–∏—Å–∫—É –∫–Ω–∏–≥ (–æ—Ç—Ä–∏–º—É—î—à –≤–∂–µ –≤—ñ–¥—Ñ—ñ–ª—å—Ç—Ä–æ–≤–∞–Ω–∏–π –∞–±–æ –æ–±—Ä—ñ–∑–∞–Ω–∏–π –º–∞—Å–∏–≤)
export function renderBookCardlist(topbooks) {
  const markupBooks = topbooks
    .map(
      ({
        _id,
        book_image,
        description = 'description || title',
        title,
        author,
        price,
      }) => `<li class="books-card" data-id="${_id}">
        <img class="books-card-image" src="${book_image}" alt="${description}" />
        <div class="books-card-text-wrapper">
          <h3 class="books-card-title">${title}</h3>
          <p class="books-card-author">${author}</p>
        </div>
        <p class="books-card-price">$${price}</p>
        <button class="books-card-btn" type="button">Learn More</button>
      </li>`
    )
    .join('');
  refs.bookCardList.innerHTML = markupBooks;
}

export function renderBookModal(book) {
  return `
    <div class="book-modal-wrapper">
      <img
        src="${book.book_image}"
        alt="${book.title}"
        class="book-modal-image"
      />
      <h2 class="book-modal-title">${book.title}</h2>
      <p class="book-modal-author">${book.author}</p>
      <p class="book-modal-price">$${book.price}</p>

      <div class="book-modal-actions">
        <div class="quantity-control">
          <button class="qty-btn" data-action="decrease">-</button>
          <span class="qty-value">1</span>
          <button class="qty-btn" data-action="increase">+</button>
        </div>
        <button class="add-to-cart-btn">Add To Cart</button>
        <button class="buy-now-btn">Buy Now</button>
      </div>

      <div class="book-modal-details">
        <details>
          <summary>Details</summary>
          <p>${book.description || book.title}</p>
        </details>
        <details>
          <summary>Shipping</summary>
          <p>We ship across the United States within 2‚Äì5 business days. All orders are processed through USPS or a reliable courier service. Enjoy free standard shipping on orders over $50.</p>
        </details>
        <details>
          <summary>Returns</summary>
          <p>You can return an item within 14 days of receiving your order, provided it hasn‚Äôt been used and is in its original condition. To start a return, please contact our support team ‚Äî we‚Äôll guide you through the process quickly and hassle-free.</p>
        </details>
      </div>
    </div>
  `;
}

// –Ü–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–∞—Ü—ñ—è –ø–æ–≤–µ–¥—ñ–Ω–∫–∏ dropdown (–¥–æ–¥–∞—î —Å–ª—É—Ö–∞—á—ñ –æ–¥–∏–Ω —Ä–∞–∑)
export function initDropdownBehavior() {
  const toggle = refs.dropdownToggle;
  const menu = refs.dropdownMenu;

  if (!toggle || !menu) return;

  // –Ø–∫—â–æ –≤–∂–µ —ñ–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–æ–≤–∞–Ω–æ ‚Äî –Ω–µ –¥–æ–¥–∞—î–º–æ —Å–ª—É—Ö–∞—á—ñ –≤–¥—Ä—É–≥–µ
  if (toggle.dataset.dropdownInited === 'true') return;

  // –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è / –∑–∞–∫—Ä–∏—Ç—Ç—è –º–µ–Ω—é
  toggle.addEventListener('click', e => {
    e.stopPropagation();
    menu.classList.toggle('open');
    toggle.classList.toggle('active');
  });

  // –≤–∏–±—ñ—Ä –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó
  menu.addEventListener('click', e => {
    const item = e.target.closest('.dropdown-item');
    if (!item) return;
    toggle.textContent = item.textContent;
    menu.classList.remove('open');
    toggle.classList.remove('active');

    // –∫–æ—Ä–∏—Å–Ω–∏–π —Ö—É–∫: –¥–∏—Å–ø–∞—Ç—á–∏–º–æ –≤–ª–∞—Å–Ω—É –ø–æ–¥—ñ—é, —â–æ–± —ñ–Ω—à—ñ –º–æ–¥—É–ª—ñ –º–æ–≥–ª–∏ –≤—ñ–¥—Ä–µ–∞–≥—É–≤–∞—Ç–∏
    const category = item.dataset.category;
    const event = new CustomEvent('category:selected', {
      detail: { category },
    });
    document.dispatchEvent(event);
  });

  // –∞–≤—Ç–æ–∑–∞–∫—Ä–∏—Ç—Ç—è –ø—Ä–∏ –∫–ª—ñ–∫—É –ø–æ–∑–∞ –º–µ–Ω—é
  document.addEventListener('click', e => {
    if (!menu.contains(e.target) && !toggle.contains(e.target)) {
      menu.classList.remove('open');
      toggle.classList.remove('active');
    }
  });

  // –º—ñ—Ç–∫–∞, —â–æ –≤–∂–µ —ñ–Ω—ñ—Ü—ñ–∞–ª—ñ–∑–æ–≤–∞–Ω–æ
  toggle.dataset.dropdownInited = 'true';
}
